.margin-top {
  margin-top: 46px;
}

.how-its-made-section {
  padding-top: 125px;
  padding-bottom: 145px;
  background-color: #fff5f6;
  position: relative;
}
.him-img {
  display: block;
  width: 100%;
  max-height: 403px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  transform: translateY(64px);

  @media screen and (min-width: 320px) {
    transform: translateY(74px);
  }
}
@media screen and (min-width: 320px) {
  .him-img {
    width: 320px;
    height: 122px;
  }
}
.how-its-made-h2 {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.16;
  text-align: center;
  text-transform: uppercase;
  color: $contacts-pink-color;
  margin: 0;
}
.how-its-made-h1 {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $primary-accent-color;
  width: 200px;
  margin-top: 15px;
  margin-right: auto;
  margin-bottom: 26px;
  margin-left: auto;
}

.how-its-made-div {
  background-image: url(../images/how-its-made/sectionbg.png);
  background-repeat: no-repeat;
  background-position: 50% 89%;
  background-size: contain;
  margin-bottom: 39px;
}
@media screen and (min-device-pixel-ratio: 2) and (min-width: 320px),
  screen and (min-resolution: 192dpi) and (min-width: 320px),
  screen and (min-resolution: 2dppx) and (min-width: 320px) {
  .how-its-made-div {
    background-image: url(../images/how-its-made/sectionbg@2x.png);
  }
}
.him-pic {
  display: flex;
  width: 100%;
  max-height: 516px;
  margin: 0 auto;
}
@media screen and (min-width: 320px) {
  .him-pic {
    width: 280px;
    height: 279px;
  }
}
.how-its-made-p {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.58;
  width: 220px;
  // margin-top: 46px;
  margin-bottom: 17px;
  margin-left: auto;
  margin-right: auto;
  .how-its-made-p-1 {
    color: $primary-black-color;
    margin-bottom: 35px;
  }
  .how-its-made-p-2 {
    color: $text-grey-color;
    margin-bottom: 15px;
  }
  .how-its-made-p-3 {
    color: $text-grey-color;
  }
}
.read-more-button {
  width: 176px;
  height: 44px;
  background: $primary-white-color;
  border-radius: 22px;
  border-color: transparent;
  padding: 0;
  display: block;
  margin: 0 auto;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  @include transition(background-color);
  &:hover {
    background-color: #e5e5e5;
  }
}
.read-more-span {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.29;
  display: flex;
  align-items: center;
  color: $primary-accent-color;
  &::after {
    content: ' ';
    display: block;
    width: 6.43px;
    height: 11px;
    fill: $primary-accent-color;
    margin-left: 13px;
    background-image: url(../images/how-its-made/vector.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-top: 19px;
    margin-bottom: 17px;
  }
}
.how-its-made-list {
  width: 320px;
  padding-left: 20px;
  margin: 0 auto;
}
.how-its-made-item {
  width: 230px;
}
.how-its-made-item:not(:last-child) {
  margin-bottom: 38px;
}
.one {
  &::before {
    content: '';
    display: block;
    padding: auto;
    width: 33px;
    height: 50px;
    background-image: url(../images/how-its-made/icon1.svg);
    margin-bottom: 16px;
  }
}
.two {
  &::before {
    content: '';
    display: block;
    padding: auto;
    width: 50px;
    height: 50px;
    background-image: url(../images/how-its-made/icon2.svg);
    margin-bottom: 16px;
  }
}
.three {
  &::before {
    content: '';
    display: block;
    padding: auto;
    width: 44px;
    height: 50px;
    background-image: url(../images/how-its-made/icon3.svg);
    margin-bottom: 16px;
  }
}
.how-its-made-item-title {
  margin: 0;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $primary-accent-color;
  margin-bottom: 14px;
}
.how-its-made-item-description {
  margin: 0;
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 1.66;
  color: $primary-black-color;
}
@media screen and (min-width: 768px) {
  .how-its-made-section {
    padding-top: 130px;
    padding-bottom: 246px;
  }
  .him-img {
    width: 768px;
    height: 258px;
    transform: translateY(155px);
  }
  .how-its-made-h2 {
    font-size: 26px;
    line-height: 1.15;
  }
  .how-its-made-h1 {
    font-size: 48px;
    line-height: 1.15;
    width: 429px;
    margin-top: 16px;
    margin-bottom: 50px;
  }
  .him-pic {
    width: 340px;
    height: 340px;
    margin: 0;
    margin-right: 17px;
  }
  .how-its-made-p {
    font-size: 14px;
    line-height: 1.72;
    width: 340px;
    margin-top: 45px;
    margin-right: 0;
    margin-bottom: 62px;
    margin-left: 0;
    .how-its-made-p-1 {
      margin-bottom: 26px;
    }
    .how-its-made-p-2 {
      margin-bottom: 10px;
    }
  }
  .how-its-made-div {
    background-image: none;
    display: flex;
    justify-content: center;
    margin-bottom: 66px;
  }
  .read-more-button {
    margin: 0;
  }
  .read-more-span {
    font-size: 16px;
    line-height: 1.31;
  }
  .how-its-made-list {
    width: 768px;
    padding-left: 36px;
    margin: 0 auto;
    display: flex;
  }
  .how-its-made-item {
    width: 180px;
    margin-bottom: 0;
    margin-right: 60px;
    &::before {
      margin-bottom: 35px;
    }
  }
  .how-its-made-item:not(:last-child) {
    margin-bottom: 0;
  }
  .how-its-made-item-title {
    font-size: 48px;
    line-height: 1.14;
    margin-bottom: 13px;
    text-transform: none;
  }
  .how-its-made-item-description {
    font-size: 14px;
    line-height: 1.7;
  }
}
@media screen and (min-width: 1200px) {
  .how-its-made-section {
    padding-top: 125px;
    padding-bottom: 310px;
  }
  .him-img {
    width: 1200px;
    height: 403px;
    transform: translateY(242px);
  }
  .how-its-made-h2 {
    font-size: 30px;
    line-height: 1.13;
  }
  .how-its-made-h1 {
    font-size: 58px;
    line-height: 1.14;
    width: auto;
    margin-top: 15px;
    margin-bottom: 55px;
  }
  .background-image {
    background-image: url(../images/how-its-made/sectionbg-desktop.png);
    background-position: 50% 19%;
    background-size: cover;
    background-repeat: no-repeat;
  }
  @media screen and (min-device-pixel-ratio: 2) and (min-width: 1200px),
    screen and (min-resolution: 192dpi) and (min-width: 1200px),
    screen and (min-resolution: 2dppx) and (min-width: 1200px) {
    .background-image {
      background-image: url(../images/how-its-made/sectionbg-desktop@2x.png);
    }
  }
  .him-pic {
    width: 516px;
    height: 516px;
    margin-right: 74px;
  }
  .how-its-made-p {
    font-size: 15px;
    line-height: 1.75;
    width: 463px;
    margin-top: 47px;
    margin-bottom: 45px;
    .how-its-made-p-1 {
      margin-bottom: 35px;
    }
    .how-its-made-p-2 {
      margin-bottom: 27px;
    }
  }
  .how-its-made-div {
    display: flex;
    justify-content: center;
    margin-bottom: 39px;
  }
  .read-more-button {
    margin: 0;
  }
  .read-more-span {
    font-size: 16px;
    line-height: 1.31;
  }
  .how-its-made-list {
    width: auto;
    padding-left: 115px;
    justify-content: center;
  }
  .how-its-made-item {
    width: 330px;
    margin-right: 30px;
    &::before {
      margin-bottom: 40px;
    }
  }
  .how-its-made-item-title {
    font-size: 58px;
    margin-bottom: 15px;
  }
  .how-its-made-item-description {
    font-size: 16px;
    line-height: 1.71;
  }
}
