.user-photo {
  display: block;
  width: 85px;
  height: 85px;
  border-radius: 50%;
  margin: 0px auto;
}
.user-comment {
  position: relative;
  // background-color: #fafafa;
}
.user-comment-text,
.user-name {
  position: relative;
  color: $primary-black-color;
  font-weight: 500;
  text-align: center;
  margin: 0px auto;
  z-index: 2;
}
.user-name {
  line-height: 1.857;
}
// .user-comment-text {
//   text-decoration: underline solid $decorative-grey-color;
// }
.quotes {
  position: absolute;

  top: 0;
  left: 0;
  width: 54px;
  height: 40px;
  fill: $decorative-grey-color;
  z-index: -1;
  transform: translate(20px, 30px);

  @media screen and (min-width: 768px) {
    transform: translate(20px, 15px);
  }
  @media screen and (min-width: 1200px) {
    transform: translate(25px, 15px);
  }
}
.dots {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 0;
}
.dot {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #c7133e;
  &:not(:last-child) {
    margin-right: 10px;
  }
  min-width: 0;
}
.comments-slider .slick-dots li {
  list-style: none;
  outline: none;
}
.comments-slider .slick-dots li:not(:last-child) {
  margin-right: 15px;
}
.comments-slider .slick-dots button:before {
  display: block;
  font-size: 0;
  width: 12px;
  height: 12px;
  background-color: #d9d9d9;
  border-radius: 50%;
  border: none;
  opacity: 1;
}
.slick-dots {
  position: static;
  display: flex;
  align-items: center;
  justify-content: center;
}
.slick-dots li.slick-active button:before {
  background-image: url(../images/slider-active-button.svg);
  background-repeat: no-repeat;
  margin: 0 auto;
  background-color: transparent;
  background-size: 12px;
  border-radius: 0;
  opacity: 1;
}

